.header {
    width: 100%;
    height: 60px;
    background: #FCFCFC;
    box-shadow: 0px 2px 10px rgba(109, 109, 109, 0.25), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}

.wrapper {
    width: 1000px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
}

.headerProfile {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.headerName {
    margin-right: 12px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    border-bottom: 1px dashed #000000;
}


